<template>
	<view class="scc">
		<!-- <view class="header">课程</view> -->
		<view style="width: 100%; height: 52rpx;"></view>
		<view class="mhd">
			<view @click="bback">
				<image style="width: 1rem; height: 1rem; margin-top: 20rpx; margin-left: 20rpx;" src="@/static/img/arrow-left-bold.png"></image>
			</view>
			<view style="font-size: 38rpx; margin-left: 10upx;">兑换记录</view>	
		</view>

		<view class="jpkc">
			<!-- <view class="jpkcTitle">发布课程</view> -->
			<view class="jpkcContent">
				<ul>
					<li @click="kcc(item.id)" v-for="(item,index) in works" :key="index">
						<view class="jpkcLeft">
							<view class="jpkcImg">
								<image style="width:180rpx; height:180rpx" :src="item.logo"></image>
							</view>
							<view class="jpkczi">
								<p style=" font-weight: bold;">{{item.tt}}…</p>
				
								<view class="jpkcBottom">
									<view style="margin-top: 20rpx; color: #7cd5c3; font-size: 24rpx;" v-if="item.qx==0">权限：免费</view>
									<view style="margin-top: 20rpx; color: #cc0033; font-size: 24rpx;" v-if="item.qx==1">权限：VIP会员</view>
									<view style="margin-top: 18rpx; color: #adadad; font-size: 24rpx;" class="jpkcPrice">{{item.up_time}}</view>
								</view>
								
							</view>
						</view>						
					</li>
					
					
				</ul>
			</view>
			<view style="height: 20rpx; width: 100%; background-color: #f2f2f2;"></view>
			
		</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				works:[],
				idds:0,
				caterggory:[],
				user_name:"",
				fl:"",
				lenn:""
				
			}
		},
		onLoad(options) {
			console.log("options")
			console.log(options)
			this.user_name=options.user_name,
			this.lenn=options.lenn
		},
		onShow(){
			// this.btn(this.idds)	
			//let that = this
			if(uni.getStorageSync("token")==""){
				console.log("空")
				uni.navigateTo({
					url:"/pages/login/login"
				})
			}else{
				if(this.fl != undefined){
					this.flagg0=false
					uni.request({
						method:'POST',
						//url:"http://wsz.52manhuage.xyz/index.php/index/select_works_all",
						url:this.apiUrl+"index.php/index/getdhjl",
						data:JSON.stringify({
							'user_name' : this.user_name
						}),
									
						success: (res) => {
							console.log("res")
							console.log(res)
							this.works = res.data
							for(let i=1; i<this.lenn+1; i++){
								console.log("i")
								console.log(i)
								console.log(this.works[i])
								this.works[i]['tt']=this.works[i].title.substring(0,32)
							}
							console.log(this.works)
						}
					})					
				}
				if(this.fl == undefined){
					uni.request({
						
						//url:"http://wsz.52manhuage.xyz/index.php/index/select_works_all",
						url:this.apiUrl+"index.php/index/select_works_all",
						success: (res) => {
							console.log("res")
							console.log(res)
							this.works = res.data
							for(let i=0; i<this.works.length; i++){
								this.works[i]['tt']=this.works[i].title.substring(0,32)
							}
							console.log(this.works)
						}
					})				
				}

				
				uni.request({
					
					//url:"http://wsz.52manhuage.xyz/index.php/index/select_works_all",
					url:this.apiUrl+"index.php/index/select_workslb_all",
					success: (res) => {
						console.log("res")
						console.log(res)
						this.caterggory = res.data
						console.log(this.caterggory)
					}
				})				
			}

		},
		methods: {
			bback(){
				uni.navigateBack(-1)
			},
			btn(index){
				
				this.flag=index+1
				console.log(this.flag)
				if(this.flag > 0){
					this.flagg0=false
					this.flagg1=false
					this.flagg2=false
				}
					uni.request({
						method:'POST',
						//url:"http://wsz.52manhuage.xyz/index.php/index/select_works_all",
						url:this.apiUrl+"index.php/index/select_works_fl",
						data:JSON.stringify({
							'fl':this.flag
						}),
				
						success: (res) => {
							console.log("res")
							console.log(res)
							this.works = res.data
							console.log(this.works)
						}
					})				

				
			},
			btnn(id){
				if(id==0){
					this.flag="",
					this.flagg0=true
					this.flagg1=false
					this.flagg2=false
					uni.request({
						
						//url:"http://wsz.52manhuage.xyz/index.php/index/select_works_all",
						url:this.apiUrl+"index.php/index/select_works_all",
						success: (res) => {
							console.log("res")
							console.log(res)
							this.works = res.data
							console.log(this.works)
						}
					})
				}
				if(id==1){
					this.flag="",
					this.flagg0=false
					this.flagg1=true
					this.flagg2=false					
				}
				if(id==2){
					this.flag="",
					this.flagg0=false
					this.flagg1=false
					this.flagg2=true					
				}
			},
	
			kcc(id){
				console.log(id)
				uni.navigateTo({
					url:"/pages/kecheng/kechengDetail?id="+id
				})
			},
			serchh(){
				console.log("title")
				console.log(this.tittle)
				uni.request({
					method:"POST",
					url:this.apiUrl+"index.php/index/ss_works",
					data:JSON.stringify({
						"title" : this.tittle
					}),
					success: (res) => {
						this.works = res.data
					}
				})
			}
		}
	}
</script>

<style>
.scc .mhd{
	width:100%;
	height: 3rem;
	display: flex;
	align-items: center;
}
 	.scc .header .iig{
		margin-top: 104rpx;
		position: relative;
		top: 10rpx;
		left: -120rpx;
	}
	.scc .header input{
		border:none;
		width: 50%;
		margin: 0 auto;
		border-radius: 80rpx;
		background-color: #f2f2f2;
		 margin-top: 104rpx;	
		padding-left: 90rpx;
		color: #bababa;
		box-shadow: 0 0 3px 2px #ededed;
	}
	.scc .immg{
		position: relative;
		top: 104rpx;
		left: 130rpx;
	}
.scc .catergory{
	display: flex;
	margin-top: 20rpx;
}
.scc .catergory .catergoryL{
	font-weight: bold;
	margin-left: 28rpx;
	
	width: 80rpx;
}
.scc .catergory .catergoryR{
	font-size: 30rpx;
}
.scc .catergory .catergoryR ul{
	display: flex;
}
.scc .catergory .catergoryR ul li{
	padding: 6rpx 14rpx;
	margin-left: 32rpx;
}
.active{
	background-color: #ffe3c6;
	color: #ff6600;
	padding: 6rpx 14rpx;
	border-radius: 30rpx;
}
	.scc .jpkc{
		margin-top: 78rpx;
		font-size: 28rpx;
	}
	.scc .jpkc .jpkcTitle{
		width: 95%;
		font-weight: bold;
		margin: 0 auto;
	}
	.scc .jpkc .jpkcContent{
		width: 95%;
		margin: 0 auto;
	}
	.scc .jpkc .jpkcContent ul li{
		display: flex;
		background-color: #fff;
		justify-content: space-between;
		margin-top: 36rpx;
		padding: 20rpx 0;
		box-shadow: 0 0 4px 4px rgba(240,240,240,0.5);
		border-radius: 10rpx;
	}
	.scc .jpkc .jpkcContent ul li:first-child{
		margin-top: 30rpx;
	}
	.scc .jpkc .jpkcContent ul li .jpkcLeft{
		display: flex;
	}
	.scc .jpkc .jpkcContent ul li .jpkcLeft .jpkczi{
		margin-left: 14rpx;
		width: 440rpx;
	
	}


	.scc .jpkc .jpkcContent ul li .jpkcLeft .jpkcImg{
		border: 2rpx solid #f8f8f8;
	}
</style>
